<template>
  <ConfirmPopup />
  <PageLayout title="Qun l ti khon" subtitle="Xem v qun l danh sch ti khon ngi dng"
    :breadcrumbs="[{ label: 'Qun l ti khon' }]">
    <!-- List Panel -->
    <Panel header="Danh sch ti khon" class="card">
      <!-- Toolbar -->
      <div class="toolbar">
        <span class="p-input-icon-left">
          <i class="pi pi-search" />
          <InputText v-model.lazy="filters.global.value as any" placeholder="Tm theo tn hoc ti khon" />
        </span>
        <Button label="Xo b lc" class="btn-outline" @click="resetFilters" />
      </div>

      <!-- DataTable -->
      <DataTable :loading="isLoading" :value="userList" :paginator="true" stripedRows :rows="10"
        :rowsPerPageOptions="[10, 25, 50]" :rowHover="true" v-model:filters="filters"
        paginatorTemplate="FirstPageLink PrevPageLink PageLinks NextPageLink LastPageLink CurrentPageReport RowsPerPageDropdown"
        :globalFilterFields="['fullname', 'username']"
        currentPageReportTemplate="Hin th {first} - {last} ca {totalRecords} ti khon">
        <Column field="fullname" header="H v tn" sortable />
        <Column field="username" header="Ti khon" sortable />
        <Column field="permission" header="Quyn hn" sortable />
        <Column header="Hnh ng" bodyStyle="text-align: center; width: 100px">
          <template #body="{ data }">
            <Button label="Chnh sa" icon="pi pi-pencil" class="btn-primary" @click="openEditDialog(data.id)"
              style="width: 100%" />
          </template>
        </Column>
      </DataTable>
    </Panel>

    <!-- Edit Dialog -->
    <Dialog v-model:visible="isDialogOpen" :style="{ width: '90vw' }"
      :breakpoints="{ '1500px': '70vw', '1150px': '80vw' }" header="Cp nht ti khon" modal>
      <div class="p-fluid p-formgrid p-grid">
        <div class="p-field p-col-12 p-md-6">
          <label>Tn ng nhp</label>
          <InputText v-model="formData.username" type="text" />
        </div>
        <div class="p-field p-col-12 p-md-6">
          <label>Mt khu (ti thiu 6 k t)</label>
          <Password v-model="formData.password" toggleMask :feedback="true" placeholder=" trng nu khng i" />
        </div>
        <div class="p-field p-col-12 p-md-6">
          <label>H v tn</label>
          <InputText v-model="formData.fullname" type="text" />
        </div>
        <div class="p-field p-col-12 p-md-6">
          <label>Quyn hn</label>
          <Dropdown v-model="formData.permission" :options="PERMISSION_OPTIONS" optionLabel="label" optionValue="value"
            placeholder="Chn quyn hn" />
        </div>
      </div>

      <template #footer>
        <Button label="Cp nht" icon="pi pi-sync" class="btn-primary" @click="saveUser" />
        <Button label="Xo" icon="pi pi-trash" class="btn-danger" @click="confirmDelete" style="margin-left: 8px" />
        <Button label="Hu" icon="pi pi-times" class="btn-outline" @click="closeDialog" style="margin-left: 8px" />
      </template>
    </Dialog>
  </PageLayout>
</template>

<script setup lang="ts">
import { ref, reactive, onMounted } from 'vue'
import { useStore } from 'vuex'
import { useRouter } from 'vue-router'
import { useToast } from 'primevue/usetoast'
import { useConfirm } from 'primevue/useconfirm'
import { FilterMatchMode } from 'primevue/api'
import AuthRepository from '@/services/AuthRepository'
import PageLayout from '@/components/PageLayout.vue'
import type { UserDetail } from '@/models/userDetail'
import type { UserUpdate } from '@/models/userUpdate'

// ===== CONSTANTS =====
const PERMISSION_OPTIONS = [
  { label: 'Qun l', value: 'admin', param: 1 },
  { label: 'Nhn vin', value: 'user', param: 2 }
]

// ===== SETUP =====
const store = useStore()
const router = useRouter()
const toast = useToast()
const confirm = useConfirm()

// Redirect if not admin
if (store.state.permission !== 'admin') {
  router.push({ name: 'home' })
}

// ===== STATE =====
const userList = ref<UserDetail[]>([])
const isLoading = ref(false)
const isDialogOpen = ref(false)

const filters = ref({
  global: { value: null, matchMode: FilterMatchMode.CONTAINS }
})

const formData = reactive<UserUpdate>({
  id: 0,
  username: '',
  password: '',
  fullname: '',
  permission: ''
})

// ===== METHODS =====

/**
 * Load users from API
 */
const loadUsers = async () => {
  try {
    isLoading.value = true
    const response = await AuthRepository.getListUser()
    userList.value = response.data
  } catch (error: any) {
    showErrorToast('Li', error?.response?.data?.message || 'Khng th ti d liu')
  } finally {
    isLoading.value = false
  }
}

/**
 * Open dialog to edit user
 */
const openEditDialog = async (userId: string) => {
  try {
    const response = await AuthRepository.getUserById(userId)
    Object.assign(formData, response.data, { password: '' })
    isDialogOpen.value = true
  } catch (error: any) {
    showErrorToast('Li', error?.response?.data?.message || 'Khng th ti d liu ngi dng')
  }
}

/**
 * Save user changes
 */
const saveUser = async () => {
  // Validate required fields
  if (!formData.username?.trim() || !formData.fullname?.trim()) {
    showErrorToast('Li', 'Vui lng in tt c trng bt buc')
    return
  }

  // Validate password if provided
  if (formData.password && formData.password.length < 6) {
    showErrorToast('Li', 'Mt khu phi ti thiu 6 k t')
    return
  }

  try {
    await AuthRepository.updateUser(formData)
    showSuccessToast('Cp nht thng tin ti khon thnh cng')
    closeDialog()
    await loadUsers()
  } catch (error: any) {
    showErrorToast('Li cp nht', error?.response?.data?.message || 'Cp nht tht bi')
  }
}

/**
 * Delete user with confirmation
 */
const confirmDelete = () => {
  confirm.require({
    message: 'Bn c chc mun xo ti khon ny khng?',
    header: 'Xc nhn xo',
    icon: 'pi pi-exclamation-triangle',
    acceptClass: 'p-button-danger',
    acceptLabel: 'Xo',
    rejectLabel: 'Hu',
    accept: () => deleteUser(),
    reject: () => { } // No-op for cancel
  })
}

/**
 * Delete user from API
 */
const deleteUser = async () => {
  try {
    await AuthRepository.deleteUser(formData.id)
    showSuccessToast('Ti khon  c xo thnh cng')
    closeDialog()
    await loadUsers()
  } catch (error: any) {
    showErrorToast('Li xo', error?.response?.data?.message || 'Xo tht bi')
  }
}

/**
 * Close dialog and reset form
 */
const closeDialog = () => {
  isDialogOpen.value = false
  Object.assign(formData, {
    id: '',
    username: '',
    password: '',
    fullname: '',
    permission: ''
  })
}

/**
 * Reset all filters
 */
const resetFilters = () => {
  filters.value = {
    global: { value: null, matchMode: FilterMatchMode.CONTAINS }
  }
}

// ===== TOAST HELPERS =====

/**
 * Show success toast message
 */
const showSuccessToast = (message: string) => {
  toast.add({
    severity: 'success',
    summary: 'Thnh cng',
    detail: message,
    life: 2000
  })
}

/**
 * Show error toast message
 */
const showErrorToast = (title: string, message?: string) => {
  toast.add({
    severity: 'error',
    summary: title,
    detail: message || 'C li xy ra',
    life: 3000
  })
}

// ===== LIFECYCLE =====
onMounted(() => {
  loadUsers()
})
</script>

<style scoped lang="scss">
.toolbar {
  display: flex;
  gap: var(--spacing-sm);
  align-items: center;
  justify-content: space-between;
  margin-bottom: var(--spacing-lg);

  .p-input-icon-left {
    flex: 1;
    max-width: 300px;
  }
}

@media (max-width: 768px) {
  .toolbar {
    flex-direction: column;
    align-items: stretch;

    .p-input-icon-left {
      width: 100%;
      max-width: 100%;
    }
  }
}
</style>