<template>


  <div id="login_page">
    <div class="wraplogin">
        <div class="main_login">
            <div class="img_lg">
              <img src="../assets/logo_vinh.png" width="70" class="img_l" alt="logo cong ty">
<!--              <img src="../assets/logo_kcl.jpg" width="70" class="img_r" alt="logo don vi">-->
            </div>    
              <h4>Bnh vin a khoa Thnh ph Vinh</h4>
              <h5>ng nhp h thng</h5>
              <div class="it_lg">
                <InputText id="firstname" type="text" v-model="userName" placeholder="Tn ng nhp"/>
              </div>
              <div class="it_lg">
                <InputText id="lastname" type="password" v-model="password" v-on:keyup.enter="doLogin()" placeholder="Mt khu"/>
              </div>
              <div class="it_lg it_lgbt">
                <Button v-if="valid" label="ng nhp" icon="pi pi-sign-in" iconPos="left" @click="doLogin()" />
                <Button v-else label="ng nhp" icon="pi pi-sign-in" iconPos="left" disabled="disabled" />
              </div>
          
        </div>
    </div>
    
  </div>

</template>


<script lang="ts">
import { computed, ref } from 'vue'
import { useStore } from 'vuex'
import { useToast } from 'primevue/usetoast'
import { useRouter } from 'vue-router'

export default {
  setup () {
    const router = useRouter()
    const store = useStore()
    const toast = useToast()

    const userName = ref('')
    const password = ref('')

    // vn kim tra c nhp user & pass th mi cho bm login
    const valid = computed(() => !!userName.value && !!password.value)

    const doLogin = () => {
      console.log('######## FAKE LOGIN  b qua backend ########')
      console.log('userName =', userName.value)

      // === FAKE TOKEN & PERMISSION ===
      // Sau ny ni backend tht th b on ny, dng li AuthRepository nh c
      store.dispatch('setToken', 'dummy-token')
      store.dispatch('setPermission', 'admin') // hoc 'user' ty bn mun test quyn g
      // ================================

      toast.add({
        severity: 'success',
        summary: 'ng nhp',
        detail: 'ng nhp gi  test giao din (khng gi backend)',
        life: 1000
      })

      router.push({
        name: 'home'   // route sau khi ng nhp
      })
    }

    return {
      doLogin,
      valid,
      password,
      userName
    }
  }
}
</script>


<style lang="scss">
  #template {
    background-color: #2196F3;
  }
</style>
